export const landingContent = async () => {
  const headerContent = document.getElementById('header');

  try {
    const response = await fetch('./data/content.json');
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }
    const content = await response.json();
    const title = content.title;
    const contentHtml = content.content;
    const btnOneText = content.btnone[0].name;
    const btnTwoText = content.btntwo[0].name;
    const btnOneUrl = content.btnone[0].href;
    const btnTwoUrl = content.btntwo[0].href;

    const btnOne = document.createElement('button');
    btnOne.classList.add('btn', 'btn-pri', 'rounded-pill');
    btnOne.textContent = btnOneText;

    const btnTwo = document.createElement('button');
    btnTwo.classList.add('btn', 'btn-pri-in', 'rounded-pill');
    btnTwo.textContent = btnTwoText;

    const btnOneLink = document.createElement('a');
    btnOneLink.href = btnOneUrl;
    btnOneLink.target = '_blank';
    btnOneLink.rel = 'noreferrer';
    btnOneLink.appendChild(btnOne);

    const btnTwoLink = document.createElement('a');
    btnTwoLink.href = btnTwoUrl;
    btnTwoLink.target = '_blank';
    btnTwoLink.rel = 'noreferrer';
    btnTwoLink.appendChild(btnTwo);

    const btnContainer = document.createElement('div');
    btnContainer.classList.add('d-flex', 'justify-content-start', 'gap');
    btnContainer.appendChild(btnOneLink);
    btnContainer.appendChild(btnTwoLink);

    headerContent.innerHTML = `<h1>${title}</h1><p>${contentHtml}</p>`;
    headerContent.appendChild(btnContainer);
  } catch (error) {
    console.error();
  }
}
