export const sideBar = async () => {
  window.onload = function() {
  // Get the sidebar element
  const sidebar = document.querySelector('.bd-sidebar ul');
  const main = document.querySelector('.bd-main');

  // Fetch the JSON data
  fetch('./data/sideBarData.json')
    .then((response) => response.json())
    .then((data) => {
      // Loop through the data and create the HTML structure
      data.forEach((section) => {
        const sectionLi = document.createElement('li');
        const sectionButton = document.createElement('button');
        const sectionCollapse = document.createElement('div');
        const sectionLinksUl = document.createElement('ul');
        sectionLinksUl.classList.add(
          'list-unstyled',
          'fw-normal',
          'pb-1',
          'small',
          'd-link'
         
          
        );
      

        // Set the attributes and text content
        sectionButton.classList.add(
          'btn',
          'nav',
          'd-inline-flex',
          'align-items-center',
          'rounded',
          'collapsed',
          'pri-head',
          'fw-bold',
          'pe-4',
          'ps-3'
        );
        sectionButton.setAttribute('data-bs-toggle', 'collapse');
        sectionButton.setAttribute(
          'data-bs-target',
          `#${section.name.replace(' ', '-')}-collapse`
        );
        sectionButton.setAttribute('aria-expanded', 'false');
        sectionButton.setAttribute(
          'aria-controls',
          `${section.name.replace(' ', '-')}-collapse`
        );
        sectionButton.id = `${section.name.replace(' ', '-')}-button`;

        const sectionText = document.createTextNode(section.name);
        sectionButton.appendChild(sectionText);

        sectionCollapse.classList.add('collapse', 'ms-5', 'show');
        sectionCollapse.id = `${section.name.replace(' ', '-')}-collapsed`;

        section.links.forEach((link) => {
          const linkLi = document.createElement('li');
          const linkA = document.createElement('a');
          linkA.classList.add('d-inline-flex', 'align-items-center', 'rounded');
          linkA.textContent = link.name;
          linkA.href = `${window.location.href}/${link.href}`;

          linkLi.classList.add('py-1', 'li-hight');

          // Load link on select

          linkA.addEventListener('click', (event) => {
            event.preventDefault(); // prevent default link behavior
            const url = linkA.href;
            const currentUrl = window.location.href;
            if (currentUrl === url) {
              console.log(`Link already active: ${url}`);
              return;
            }
            if (localStorage.getItem(url)) {
              console.log(`Loading ${url} from localStorage`);
              main.innerHTML = localStorage.getItem(url);
              history.pushState(null, '', url); // update the URL in the browser's history
              window.location.reload(); // reload the page
            } else {
              console.log(`Loading ${url} from server`);
              fetch(url)
                .then((response) => response.text())
                .then((html) => {
                  main.innerHTML = html;
                  localStorage.setItem(url, html); // store the content in localStorage
                  history.replaceState(null, '', url);
                  window.location.reload(); // reload the page
                })
                .catch((error) => console.error(error));
            }
            // add active class to clicked link
            const links = document.querySelectorAll('.d-link li a');
            links.forEach((link) => {
              if (link === linkA) {
                link.classList.add('active-link');
              } else {
                link.classList.remove('active-link');
              }
            });
          });

          window.onload = () => {
            const currentUrl = window.location.href;
            const links = document.querySelectorAll('.d-link li a');
            links.forEach((link) => {
              if (link.href === currentUrl) {
                link.classList.add('active-link');
              } else {
                link.classList.remove('active-link');
              }
            });
          };

          linkLi.appendChild(linkA);
          sectionLinksUl.appendChild(linkLi);
        });

        sectionCollapse.appendChild(sectionLinksUl);
        sectionLi.appendChild(sectionButton);
        sectionLi.appendChild(sectionCollapse);
        sidebar.appendChild(sectionLi);

       
      });
      // After creating the HTML structure, initialize the Feather icons
      feather.replace();
    })
    .catch(() => console.error());
    };
};
